// CRUD trong ReactJS
import React,{useState, useEffect} from "react";
import ReactDOM from "react-dom";
//import '../components/Vdu6.css';
import './Vdu6.css';
import axios from 'axios';
import {API_ROOT} from './api-config';

const InputDiv = ({parentCallback}) => {
    const [list, setList] = useState('');
    const updateState = e => setList(e.target.value);
    
    const send=()=>{
        const newValue = list;
        //parentCallback(newValue);
        //console.log(newValue);

        //cap nhat data tu reactjs len nodejs  
        const user = list;

        axios.post(`${API_ROOT}/add`, { user })
            .then((res) => {
                //console.log(res.data);
                parentCallback(newValue);
            }).catch((error) => {
                console.log(error);
        });
        
        ReactDOM.unmountComponentAtNode(document.getElementById('divInput')); // an component InputDiv
    }

    return (
        <div>
            <input type="text" placeholder="Enter your name!" onChange = {updateState}/>   
            <button onClick={send}>Gui</button> 
        </div>
    );
}





const List = () => {
    
    //const initialValue = ["Hi","Hello","Xin Chao"];
    const initialValue = [];
    const [values,setValue] = useState(initialValue);

    // Cach 1: su dung fetch data thong thuong
    // useEffect(() => {
    //     // POST request using fetch inside useEffect React hook
    //     const requestOptions = {
    //         method: 'POST',
    //         headers: { 'Content-Type': 'application/json' },
    //         body: JSON.data
    //     };
        
    //     fetch(`${API_ROOT}/getNotes`, requestOptions)
    //         .then(response => response.json())
    //         .then(data => setValue(data));
    // // empty dependency array means this effect will only run once (like componentDidMount in classes)
    // }, []);

    //Cach 2: su dung axios de fetch data
    useEffect(() => {
        axios.post(`${API_ROOT}/getNotes`, data => setValue(data)).then(json => setValue(json.data));
    }, [])


    
    const addDiv = () =>{ // ham global
        
        //alert("clicked");
        const callback = (item) => {
            // do something with value in parent component, like save to state
            //console.log(item);
            setValue(values=>[...values,item]);
        }
        ReactDOM.render(<InputDiv parentCallback={callback}/>, document.getElementById('divInput'));
    }
    
    const deleteID = (index) =>{
        //axios.post('https://reactonline.herokuapp.com/delete', {idXoa: index})
        axios.post(`${API_ROOT}/delete`, {idXoa: index})
            .then((res) => {
                //console.log(res.data);
                setValue(res.data);
            }).catch((error) => {
                console.log(error);
        });
    }
    
    const handleChange = (newValue, index) => {
        //console.log('do something: ', newValue);
        //console.log('do num: ', index);

        //axios.post('https://reactonline.herokuapp.com/update', {idSua: index, noiDung: newValue})
        axios.post(`${API_ROOT}/update`, {idSua: index, noiDung: newValue})
            .then((res) => {
                //console.log(res.data);
                setValue(res.data);
            }).catch((error) => {
                console.log(error);
        });
      } 

    return(
        <div className ="div-List">
            <div id="divInput"></div>
            <button onClick={addDiv}> Them </button>
            {
                values.map(function(note,index){
                   return (
                    <Note key={index} 
                        id={index}
                        xoaID={()=>deleteID(index)} 
                        onClick={handleChange}                        
                    >
                        {note}
                    </Note>
                   );

                })
            }
        </div>
    );
}

const Note = (props) => {
    
    const [flag, setflag ] = useState(false);
    const [txt, setTxt] = useState("");

    const suaItem = () => {
        setflag(true);
    }

    const cancel = () => {
        setflag(false);
    }

    const onInputChange = e => {
        setTxt(e.target.value);
    }

    function handleChange(event) {
        // Here, we invoke the callback with the new value
        props.onClick(txt, props.id);
        setflag(false);
    }

    if(flag){
        return (
            <div className ="div-Note">
                <input defaultValue={props.children} onChange={onInputChange}/>
                <button onClick={handleChange}>Luu Item</button> 
                <button onClick={cancel}>Huy Item</button>
            </div>
        );
    }
    else{
        return (
            <div className ="div-Note">
                <p>{props.children}</p>
                <button onClick={props.xoaID}>Xoa Item</button>
                <button onClick={suaItem}>Sua Item</button>
            </div>
        );
    }
    
}

export default List;